/*global chrome*/
import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import TagCards from './tagCards';



const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 240,
  },
}));


function getChromeStoragefunction() {
  

  chrome.storage.sync.get({ tags: [] }, function (result) {
    var tags = result.tag;
    if (typeof tags === "undefined") {
      return ""
    } else {
      result.tags.map(e => { console.log(e); })
      console.log('Value currently is ' + result.key);
      return result.tags
    }


  });
  chrome.storage.onChanged.addListener(function (changes, namespace) {
    for (var key in changes) {
      var storageChange = changes[key];
      storageChange.newValue.map(e => { console.log("new values " + e) })
      // console.log('Storage key "%s" in namespace "%s" changed. ' +
      //             'Old value was "%s", new value is "%s".',
      //             key,
      //             namespace,
      //             storageChange.oldValue,
      //             storageChange.newValue);
    }
    return storageChange.newValue
  });
}

export default function TagsPaper() {
  let savedTags = getChromeStoragefunction();
  let showTagCards;
  // useEffect(() => {
  //   // Update the document title using the browser API
  //   savedTags = getChromeStoragefunction();
  // });

  const classes = useStyles();
  if (savedTags === "") {
    showTagCards = <p>
      None Yet. Start Following Tags!
    </p>
  }


  return (
    <div className={classes.root}>
      <main className={classes.content}>

        <Container maxWidth="lg" className={classes.container}>
          <Grid container spacing={3}>

            {/* From this article */}
            <Grid item xs={12}>
              <Paper className={classes.paper}>
                {showTagCards}
                {savedTags.map((e)=>{
                  
                  <div key={e} ><TagCards tagName={e}/> <br/></div>
               })}
              </Paper>
            </Grid>
          </Grid>

        </Container>
      </main>
    </div>
  );
}